@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(#BBD2C5, #536976, #292E49); /* UIGradient name: Royal Blue + Petrol ; */
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}

.stairs {
  background:
  linear-gradient(63deg, #999 23%, transparent 23%) 7px 0,
  linear-gradient(63deg, transparent 74%, #999 78%),
  linear-gradient(63deg, transparent 34%, #999 38%, #999 58%, transparent 62%),
  #444;
  background-size: 16px 48px;
}

nav {
	display: flex; 
	justify-content: flex-end; 
	flex-wrap: wrap;
	color: #f0f0f0;
}

@media only screen and (max-width: 600px) {
  .reduce {
    font-size: 0.5em;
  }
}
.Tilt {
	background: radial-gradient(#BBD2C5, #536976, #292E49 80%); /* UIGradient name: Royal Blue + Petrol ; */
}

#logo {
	padding-top: 15px;
}


#submit {
	margin-left: 5%;
}
article {
	background: rgba(255, 255, 255, 0.25);
}

#inputimage {
	width: 900px;
	height: auto;
}

.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
}

#result {
	background: rgba(255, 255, 255, 0.5);
	display: block;
}

@media only screen and (max-width: 600px) {
	#inputimage {
		width: 500px;
		height: auto;
	}
}

@media only screen and (max-width: 500px) {
	#inputimage {
		width: 400px;
		height: auto;
	}
}

@media only screen and (max-width: 400px) {
	#inputimage {
		width: 300px;
		height: auto;
	}
}
.App {
  text-align: center;
  width: 90%;
  max-width: 900px;
  margin: 0 auto;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.container {
  display: flex; /* or inline-flex */
  justify-content: space-between;
  flex-wrap: nowrap;
}

